import { Navigate, Route, Routes } from "react-router-dom";
import UserList from "./components/User-List/UserList";
import { AppRoute } from "./const";
import ChatPage from "./Pages/ChatPage";
import HomePage from "./Pages/HomePage";
import LoginPage from "./Pages/LoginPage";
import RegistrationPage from "./Pages/RegistrationPage";
import UserPage from "./Pages/UserPage";
import { useAuth } from "../src/hooks/use-auth"
import Header from "./components/Header/Header";
import { useState } from "react";
import { db, fetchToken, onMessageListener } from "./firebase";
import { Toast } from 'react-bootstrap';
import { doc, setDoc } from "firebase/firestore";



function App(): JSX.Element {
  const [show, setShow] = useState(false);
  const [notification, setNotification] = useState({ title: '', body: '' });
  const [isTokenFound, setTokenFound] = useState(false);
  const [token, setToken] = useState<null | string>(null);
  const { isAuth, email, id, firstName, secondName } = useAuth();

  fetchToken(setTokenFound, setToken);
  if (isAuth) {
    const addUser = doc(db, 'Users', `${email}`);
    setDoc(addUser, {
      firstName: firstName,
      secondName: secondName,
      id: id,
      email: email,
      token: token
    }, { merge: true });
  }
  onMessageListener().then(payload => {
    setNotification({ title: payload.notification.title, body: payload.notification.body })
    setShow(true);
    console.log(payload);
  }).catch(err => console.log('failed: ', err));
  // eslint-disable-next-line 
  const onShowNotificationClicked = () => {
    setNotification({ title: "Notification", body: "This is a test notification" })
    setShow(true);
  }

  return (
    <>
      <Toast onClose={() => setShow(false)} show={show} delay={3000} autohide animation style={{
        position: 'absolute',
        top: 20,
        right: 20,
        minWidth: 200
      }}>
        <Toast.Header>
          <img
            src="holder.js/20x20?text=%20"
            className="rounded mr-2"
            alt=""
          />
          <strong className="mr-auto">{notification.title}</strong>
          <small>just now</small>
        </Toast.Header>
        <Toast.Body>{notification.body}</Toast.Body>
      </Toast>
      {
        isAuth ?
          <Header />
          :
          null
      }
      <Routes>
        <Route path={AppRoute.home} element={<HomePage />} />
        <Route path={AppRoute.login} element={<LoginPage />} />
        <Route path={AppRoute.registration} element={<RegistrationPage />} />
        <Route path={AppRoute.chat} element={<ChatPage />} />
        <Route path={AppRoute.userPage} element={<UserPage />} />
        <Route path={AppRoute.userList} element={<UserList />} />
        <Route path="*" element={<Navigate to={AppRoute.home} replace />} />
      </Routes>
    </>

  );
}

export default App;
