import { collection, onSnapshot, orderBy, query } from "firebase/firestore";
import { db } from "../firebase";
import { useEffect, useState } from "react";
import { useAuth } from "../hooks/use-auth";
import { Navigate } from "react-router-dom";
import { AppRoute } from "../const";
import Chat from "../components/Chat/Chat";
import _ from "lodash";

function sendNotification(title: string, options: any) {
  // Проверим, поддерживает ли браузер HTML5 Notifications
  if (!("Notification" in window)) {
    alert('Ваш браузер не поддерживает HTML Notifications, его необходимо обновить.');
  }


  else if (Notification.permission === "granted") {
    var notification = new Notification(title, options);
    function clickFunc() { alert('Пользователь кликнул на уведомление'); }
    notification.onclick = clickFunc;
  }

  else if (Notification.permission !== 'denied') {
    Notification.requestPermission(function (permission) {
      // Если права успешно получены, отправляем уведомление
      if (permission === "granted") {
        var notification = new Notification(title, options);

      } else {
        alert('Вы запретили показывать уведомления'); // Юзер отклонил наш запрос на показ уведомлений
      }
    });
  } else {
    // Пользователь ранее отклонил наш запрос на показ уведомлений
    // В этом месте мы можем, но не будем его беспокоить. Уважайте решения своих пользователей.
  }
}

function useUpdate(ChatId: any) {
  const [newMessages, setNewMessages] = useState<any[]>([]);
  useEffect(() => {
    const q = query(collection(db, `${ChatId}`), orderBy("createAt"));
    const unsubscribe = onSnapshot(q, (querySnapshot) => {
      const messages: any = [];
      querySnapshot.forEach((doc) => {
        messages.push(doc.data());
      });
      setNewMessages(messages);
    })
  }, []);
  return newMessages;
}


function ChatPage(): JSX.Element {
  const { ChatID } = useAuth();
  const newMessages = useUpdate(ChatID);
  const [messages, setMessages] = useState<any[]>([]);
  const [firstLoad, setFirstLoad] = useState<boolean>(false)
  useEffect(() => {
    if (firstLoad) {
      sendNotification('Верните Линуса!', {
        body: 'Тестирование HTML5 Notifications',
        icon: 'icon.jpg',
        dir: 'auto'
      });
    }
    setMessages(newMessages);
    setFirstLoad(true)
  }, [newMessages])
  return (
    <>
      {
        !ChatID ?
          <Navigate replace to={AppRoute.userList} />
          :
          <Chat messagesList={newMessages} />
      }
    </>
  )
}

export default ChatPage
